* { box-sizing: border-box; }

/* Design System Colors */
:root {
    --base---white: #FFFFFF;
    --base---black: #000000;
    --base---primary--action: #0056FF;
    --neutrals--50: #F9FAFC;
    --neutrals--100: #F3F4F6;
    --neutrals--200: #E6E7EB;
    --neutrals--300: #D2D5DC;
    --neutrals--400: #9CA2AE;
    --neutrals--500: #6B7380;
    --neutrals--600: #4C5564;
    --neutrals--700: #384152;
    --neutrals--800: #202938;
    --neutrals--900: #111828;
    --utility---overlay-1: #202938;
    --utility---overlay-2: #202938;

    --shadow--standard:  2px 4px 9px rgba(187, 187, 187, 0.3);
    --shadow--extreme:  2px 4px 9px rgba(0, 0, 0, 0.4);
    --border--bottom:  0px 1px 0px rgba(230, 231, 235, 1);
    --border--left:  -1px 0px 0px rgba(230, 231, 235, 1);
    --border--right:  1px 0px 0px rgba(230, 231, 235, 1);
    --border--top:  0px -1px 0px rgba(230, 231, 235, 1);
}

$breakpoint--xlarge: 1280px;
$breakpoint--large: 1200px;
$breakpoint--medium: 1024px;
$breakpoint--small: 768px;
$breakpoint--xsmall: 600px;

